#logInBody {
    background-image: url("https://i.pinimg.com/originals/00/da/d4/00dad4bddefcca3313b09988a89cf4f5.png");
}
#loginContent {
    border:1px solid red;
    border-color: red;
    box-shadow: 0 0 13px red;
    width:700px;
    height:485px;
    position:relative;
    top:100px;
    border-radius:70px;
    background:black;
}
#login {
    color:White;
    font-family:"MS PGothic";
    font-size:50px;
}
#loginError {
    color:red;
    font-family:"Bahnschrift SemiBold";
}
.info {
    width:500px;
    height:40px;
    margin-top:40px;
    position:relative;
    bottom:30px;
    outline:none;
    border:none;
    font-size:15px;
    font-family:"Bahnschrift SemiBold";
}
.info:focus {
    box-shadow: 0 0 20px red;
}
#logInBtn {
    background-image: linear-gradient(to right, #FF512F 0%, #F09819  51%, #FF512F  100%);
    font-family:"MS PGothic";
    padding:20px;
    font-size:30px;
    border-radius:30px;
    position:relative;
    transition: background-color 1s;
    top:20px;
    width:500px;
}